/* Game Area - Main Container */
.game-area {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(139, 92, 246, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(168, 85, 247, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 40% 60%, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
    linear-gradient(135deg, #0f0f23 0%, #1a1a2e 25%, #16213e 50%, #0f172a 75%, #0c1220 100%);
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  transition: background-color 0.3s ease;
}

/* Header */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  background: rgba(16, 16, 16, 0.95);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.logo {
  display: flex;
  align-items: center;
}

.logo-icon {
  width: 32px;
  height: 32px;
  background: linear-gradient(135deg, #8b5cf6, #6366f1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}

.weekly-leaderboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.weekly-leaderboard .amount {
  font-size: 14px;
  font-weight: 700;
  color: #8b5cf6;
  line-height: 1.2;
}

.weekly-leaderboard .label {
  font-size: 11px;
  font-weight: 600;
  color: #8b5cf6;
  letter-spacing: 0.5px;
}

.connect-btn {
  background: #8b5cf6;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.connect-btn:hover {
  background: #7c3aed;
  transform: translateY(-1px);
}

/* Jackpot Section */
.jackpot-section {
  padding: 24px 0;
  background: rgba(15, 15, 35, 0.8);
  backdrop-filter: blur(20px);
  text-align: center;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid rgba(139, 92, 246, 0.2);
  position: relative;
}

.jackpot-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.05) 0%, rgba(168, 85, 247, 0.05) 100%);
  pointer-events: none;
}

.jackpot-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-bottom: 12px;
  padding: 0 16px;
  position: relative;
  z-index: 2;
}

.jackpot-header h2 {
  font-size: 24px;
  font-weight: 800;
  color: white;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 10px;
  background: linear-gradient(135deg, #ffffff 0%, #8b5cf6 50%, #a855f7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 30px rgba(139, 92, 246, 0.5);
  letter-spacing: 1px;
}

.automation-badge {
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.3) 0%, rgba(34, 197, 94, 0.3) 100%);
  color: #10b981;
  padding: 4px 8px;
  border-radius: 8px;
  font-size: 11px;
  font-weight: 700;
  border: 1px solid rgba(16, 185, 129, 0.4);
  box-shadow: 0 2px 8px rgba(16, 185, 129, 0.3);
  animation: automationPulse 2s ease-in-out infinite;
  letter-spacing: 0.5px;
}

@keyframes automationPulse {
  0%, 100% { 
    transform: scale(1); 
    box-shadow: 0 2px 8px rgba(16, 185, 129, 0.3);
  }
  50% { 
    transform: scale(1.05); 
    box-shadow: 0 4px 12px rgba(16, 185, 129, 0.5);
  }
}

.player-count {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.3) 0%, rgba(168, 85, 247, 0.3) 100%);
  color: #ffffff;
  padding: 6px 12px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 700;
  border: 1px solid rgba(139, 92, 246, 0.4);
  box-shadow: 
    0 4px 20px rgba(139, 92, 246, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
  animation: playerCountPulse 3s ease-in-out infinite;
  min-width: 2ch;
  text-align: center;
  transition: all 0.3s ease;
}

@keyframes playerCountPulse {
  0%, 100% { transform: scale(1); box-shadow: 0 4px 20px rgba(139, 92, 246, 0.3); }
  50% { transform: scale(1.05); box-shadow: 0 6px 25px rgba(139, 92, 246, 0.5); }
}

.jackpot-subtitle {
  color: rgba(255, 255, 255, 0.8);
  font-size: 16px;
  margin: 0 0 24px 0;
  padding: 0 16px;
  font-weight: 500;
  letter-spacing: 0.5px;
  position: relative;
  z-index: 2;
  min-height: 24px;
  transition: opacity 0.3s ease;
}

.bet-controls {
  display: flex;
  flex-direction: column;
  gap: 16px;
  justify-content: center;
  align-items: center;
  padding: 0 16px;
  position: relative;
  z-index: 2;
  min-height: 100px;
}

.bet-amount-display {
  display: flex;
  align-items: center;
  gap: 12px;
  background: linear-gradient(135deg, rgba(15, 15, 35, 0.9) 0%, rgba(26, 26, 46, 0.9) 100%);
  padding: 16px 24px;
  border-radius: 16px;
  border: 2px solid rgba(139, 92, 246, 0.4);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 20px rgba(139, 92, 246, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  position: relative;
  overflow: hidden;
  min-width: 80px;
  text-align: center;
}

.bet-amount-display::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.1), transparent);
  transition: left 0.8s ease;
}

.bet-amount-display:hover::before {
  left: 100%;
}

.bet-amount-value {
  font-size: 22px;
  font-weight: 800;
  color: white;
  min-width: 40px;
  text-align: center;
  text-shadow: 0 2px 10px rgba(139, 92, 246, 0.5);
}

.currency-label {
  font-size: 14px;
  color: rgba(139, 92, 246, 0.9);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.bet-buttons {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  justify-content: center;
}

.bet-preset {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.15) 0%, rgba(168, 85, 247, 0.15) 100%);
  color: #ffffff;
  border: 1px solid rgba(139, 92, 246, 0.3);
  padding: 12px 20px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 60px;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.bet-preset::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.2) 0%, rgba(168, 85, 247, 0.2) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.bet-preset:hover::before {
  opacity: 1;
}

.bet-preset:hover {
  border-color: rgba(139, 92, 246, 0.6);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(139, 92, 246, 0.3);
}

.bet-preset:active {
  transform: translateY(0);
}

.bet-button {
  background: linear-gradient(135deg, #8b5cf6 0%, #a855f7 50%, #c084fc 100%);
  color: white;
  border: none;
  padding: 16px 40px;
  border-radius: 16px;
  font-size: 18px;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 
    0 8px 30px rgba(139, 92, 246, 0.4),
    inset 0 2px 0 rgba(255, 255, 255, 0.2);
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.bet-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
}

.bet-button:hover::before {
  left: 100%;
}

.bet-button:hover {
  background: linear-gradient(135deg, #7c3aed 0%, #9333ea 50%, #a855f7 100%);
  transform: translateY(-3px);
  box-shadow: 
    0 12px 40px rgba(139, 92, 246, 0.5),
    inset 0 2px 0 rgba(255, 255, 255, 0.3);
}

.bet-button:active {
  transform: translateY(-1px);
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 16px;
  padding: 20px;
  background: rgba(8, 8, 8, 0.9);
  width: 100%;
  box-sizing: border-box;
  backdrop-filter: blur(20px);
  min-height: 120px;
}

.stat-card {
  background: linear-gradient(135deg, rgba(24, 24, 27, 0.9) 0%, rgba(15, 15, 35, 0.9) 100%);
  border: 1px solid rgba(139, 92, 246, 0.25);
  border-radius: 16px;
  padding: 20px;
  text-align: left;
  position: relative;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(20px);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.05);
  overflow: hidden;
  min-width: 120px;
  min-height: 80px;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.6), transparent);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.stat-card:hover::before {
  opacity: 1;
}

.stat-card:hover {
  border-color: rgba(139, 92, 246, 0.5);
  transform: translateY(-4px) scale(1.02);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.4),
    0 0 30px rgba(139, 92, 246, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.stat-card.jackpot-value,
.stat-card.net-payout,
.stat-card.your-wager {
  border-color: rgba(139, 92, 246, 0.4);
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.08) 0%, rgba(24, 24, 27, 0.9) 100%);
}

.stat-card.jackpot-value:hover,
.stat-card.net-payout:hover,
.stat-card.your-wager:hover {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.15) 0%, rgba(24, 24, 27, 0.9) 100%);
}

.stat-icon {
  color: #8b5cf6;
  font-size: 20px;
  margin-bottom: 12px;
  display: block;
  filter: drop-shadow(0 0 10px rgba(139, 92, 246, 0.5));
}

.stat-amount {
  font-size: 24px;
  font-weight: 800;
  color: white;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  gap: 6px;
  text-shadow: 0 2px 10px rgba(139, 92, 246, 0.3);
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
  will-change: contents;
}

.stat-label {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.stat-card.your-chance .stat-amount,
.stat-card.time-remaining .stat-amount {
  font-size: 20px;
}

.stat-card.your-chance .stat-amount {
  color: #10b981;
  text-shadow: 0 2px 10px rgba(16, 185, 129, 0.3);
}

.stat-card.time-remaining .stat-amount {
  color: #f59e0b;
  text-shadow: 0 2px 10px rgba(245, 158, 11, 0.3);
}

/* Winner Arrow */
.winner-arrow-container {
  display: flex;
  justify-content: center;
  padding: 20px 0 12px 0;
  background: rgba(8, 8, 8, 0.9);
  width: 100%;
  position: relative;
}

.winner-arrow-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.4), transparent);
}

.winner-arrow {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.arrow {
  color: #8b5cf6;
  font-size: 28px;
  filter: drop-shadow(0 0 15px rgba(139, 92, 246, 0.8));
  animation: arrowPulse 2s ease-in-out infinite;
  position: relative;
}

.arrow::after {
  content: 'WINNER';
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 10px;
  font-weight: 700;
  color: rgba(139, 92, 246, 0.8);
  letter-spacing: 2px;
  text-transform: uppercase;
}

@keyframes arrowPulse {
  0%, 100% { 
    transform: translateY(0) scale(1); 
    opacity: 0.8; 
    filter: drop-shadow(0 0 15px rgba(139, 92, 246, 0.8));
  }
  50% { 
    transform: translateY(-6px) scale(1.1); 
    opacity: 1; 
    filter: drop-shadow(0 0 25px rgba(139, 92, 246, 1));
  }
}

/* Slot Machine */
.slot-machine {
  background: rgba(8, 8, 8, 0.9);
  padding: 0 20px 20px 20px;
  width: 100%;
  box-sizing: border-box;
  backdrop-filter: blur(20px);
  transform: translateZ(0); /* Force hardware acceleration */
}

.slot-frame {
  position: relative;
  background: linear-gradient(135deg, rgba(16, 16, 16, 0.9) 0%, rgba(26, 26, 46, 0.9) 100%);
  border-radius: 16px;
  overflow: hidden;
  border: 2px solid rgba(139, 92, 246, 0.3);
  box-shadow: 
    0 12px 40px rgba(0, 0, 0, 0.4),
    inset 0 2px 0 rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  transform: translateZ(0); /* Force hardware acceleration */
}

.slot-frame::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.05) 0%, rgba(168, 85, 247, 0.05) 100%);
  pointer-events: none;
  z-index: 1;
}

/* Slot Reel Container */
.slot-reel-container {
  position: relative;
  height: 180px;
  overflow: hidden;
  padding: 25px 0;
  transform: translateZ(0); /* Force hardware acceleration */
}

.slot-reel {
  display: flex;
  gap: 20px;
  height: 100%;
  will-change: transform;
  padding: 0 25px;
  position: relative;
  z-index: 2;
  transform-origin: left center;
  transform: translateZ(0); /* Force hardware acceleration */
  backface-visibility: hidden; /* Prevent flickering */
  /* Remove transition for smooth continuous animation */
}

.slot-reel.idle {
  /* No transition needed for continuous animation */
}

.slot-reel.slot-spinning {
  transition: transform 2.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transform: translateZ(0); /* Ensure smooth horizontal slide */
}

.slot-card {
  flex-shrink: 0;
  width: 150px;
  height: 130px;
  transition: opacity 0.3s ease;
  filter: drop-shadow(0 4px 15px rgba(0, 0, 0, 0.3));
  transform-origin: center center;
  backface-visibility: hidden; /* Prevent flickering */
  transform: translateZ(0); /* Force hardware acceleration */
  position: relative;
}

.slot-card:hover {
  transform: translateY(-2px) scale(1.02);
}

/* Slot Blur Effect - Disabled to prevent rotation illusion */
.slot-blur {
  display: none; /* Completely disable blur effect */
}

/* Footer Info */
.footer-info {
  background: linear-gradient(135deg, rgba(8, 8, 8, 0.95) 0%, rgba(15, 15, 35, 0.95) 100%);
  padding: 20px;
  border-top: 1px solid rgba(139, 92, 246, 0.3);
  margin-top: auto;
  width: 100%;
  box-sizing: border-box;
  backdrop-filter: blur(20px);
  position: relative;
  min-height: 40px;
}

.footer-info::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.6), transparent);
}

.payout-info {
  text-align: center;
  color: #8b5cf6;
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow: 0 0 10px rgba(139, 92, 246, 0.5);
}

.game-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  padding: 12px 20px;
  background: rgba(26, 26, 46, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(139, 92, 246, 0.2);
  backdrop-filter: blur(10px);
  min-height: 20px;
}

.players-count {
  color: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
}

.round-number {
  color: rgba(139, 92, 246, 0.8);
  font-weight: 700;
  font-size: 14px;
  background: rgba(139, 92, 246, 0.1);
  padding: 4px 8px;
  border-radius: 6px;
  border: 1px solid rgba(139, 92, 246, 0.2);
}

/* Winner Announcement */
.winner-announcement {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.5s ease;
  backdrop-filter: blur(8px);
}

.winner-content {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  padding: 40px;
  border-radius: 20px;
  text-align: center;
  border: 2px solid #8b5cf6;
  box-shadow: 0 0 50px rgba(139, 92, 246, 0.5);
  animation: winnerSlideIn 0.8s ease;
}

.winner-content h3 {
  font-size: 28px;
  color: #ffd700;
  margin-bottom: 16px;
  text-shadow: 0 0 20px rgba(255, 215, 0, 0.6);
  animation: winnerTextGlow 1s ease-in-out infinite;
}

.winner-content p {
  font-size: 16px;
  color: white;
  margin: 0;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes winnerSlideIn {
  from {
    transform: scale(0.5) translateY(-100px);
    opacity: 0;
  }
  to {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

@keyframes winnerTextGlow {
  0%, 100% { text-shadow: 0 0 20px rgba(255, 215, 0, 0.6); }
  50% { text-shadow: 0 0 30px rgba(255, 215, 0, 0.9); }
}

/* Responsive Design */
@media (max-width: 480px) {
  .header {
    padding: 8px 12px;
  }
  
  .weekly-leaderboard .amount {
    font-size: 12px;
  }
  
  .weekly-leaderboard .label {
    font-size: 10px;
  }
  
  .connect-btn {
    padding: 6px 12px;
    font-size: 12px;
  }
  
  .jackpot-section {
    padding: 16px 0;
  }
  
  .jackpot-header h2 {
    font-size: 20px;
  }
  
  .bet-controls {
    gap: 12px;
    padding: 0 12px;
  }
  
  .bet-amount-display {
    padding: 12px 20px;
  }
  
  .bet-amount-value {
    font-size: 16px;
  }
  
  .bet-buttons {
    gap: 8px;
  }
  
  .bet-preset {
    padding: 8px 12px;
    font-size: 12px;
  }
  
  .bet-button {
    padding: 12px 24px;
    font-size: 14px;
  }
  
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 8px;
    margin: 0 12px;
  }
  
  .stat-card {
    padding: 12px;
  }
  
  .stat-amount {
    font-size: 14px;
  }
  
  .stat-card.your-chance .stat-amount,
  .stat-card.time-remaining .stat-amount {
    font-size: 14px;
  }
  
  .arrow {
    font-size: 18px;
  }
  
  .slot-machine {
    padding: 0 12px 12px 12px;
  }
  
  .slot-card {
    width: 120px;
    height: 110px;
  }
  
  .slot-reel {
    gap: 15px;
    padding: 0 20px;
    transform: translateZ(0); /* Force hardware acceleration on mobile */
    backface-visibility: hidden; /* Prevent flickering on mobile */
  }
  
  .slot-reel-container {
    height: 160px;
    padding: 20px 0;
  }
  
  .footer-info {
    padding: 16px 12px;
  }
  
  .payout-info {
    font-size: 14px;
    margin-bottom: 12px;
  }
  
  .game-stats {
    padding: 8px 12px;
    font-size: 14px;
  }
}

.automation-info {
  color: #10b981;
  font-weight: 600;
}

.connection-status {
  color: #10b981;
  font-weight: 700;
  text-shadow: 0 0 8px rgba(16, 185, 129, 0.5);
}

/* Winner Announcement Overlay */
.winner-announcement-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-in;
}

.winner-announcement-card {
  background: linear-gradient(145deg, #1a1b2e, #2a2d47);
  border: 2px solid #6366f1;
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  box-shadow: 0 20px 40px rgba(99, 102, 241, 0.3);
  animation: bounceIn 0.5s ease-out;
  max-width: 90%;
  width: 400px;
}

.winner-announcement-card .winner-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  animation: pulse 2s infinite;
}

.winner-announcement-card h3 {
  color: #fbbf24;
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  text-shadow: 0 2px 4px rgba(251, 191, 36, 0.3);
}

.winner-details {
  margin-bottom: 1.5rem;
}

.winner-address {
  color: #6366f1;
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  background: rgba(99, 102, 241, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 8px;
}

.winner-prize {
  color: #10b981;
  font-size: 1.5rem;
  font-weight: bold;
  text-shadow: 0 2px 4px rgba(16, 185, 129, 0.3);
}

.winner-confetti {
  font-size: 1.5rem;
  animation: bounce 1s infinite;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes bounceIn {
  0% {
    transform: scale(0.3) translateY(-50px);
    opacity: 0;
  }
  50% {
    transform: scale(1.05) translateY(0);
    opacity: 1;
  }
  100% {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* Mobile responsive */
@media (max-width: 480px) {
  .winner-announcement-card {
    padding: 1.5rem;
    width: 320px;
  }
  
  .winner-announcement-card .winner-icon {
    font-size: 3rem;
  }
  
  .winner-announcement-card h3 {
    font-size: 1.5rem;
  }
  
  .winner-address {
    font-size: 1rem;
  }
  
  .winner-prize {
    font-size: 1.3rem;
  }
}

/* Big Loading Overlay for Winner Selection */
.winner-selection-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  animation: fadeIn 0.3s ease-in;
  backdrop-filter: blur(10px);
}

.winner-selection-content {
  text-align: center;
  color: white;
  padding: 3rem 2rem;
  background: linear-gradient(145deg, rgba(26, 26, 46, 0.9), rgba(16, 16, 35, 0.9));
  border-radius: 24px;
  border: 2px solid rgba(139, 92, 246, 0.5);
  box-shadow: 0 20px 60px rgba(139, 92, 246, 0.3);
  max-width: 400px;
  width: 90%;
  backdrop-filter: blur(20px);
}

.loading-spinner-big {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  animation: spinAndPulse 2s ease-in-out infinite;
  filter: drop-shadow(0 0 20px rgba(139, 92, 246, 0.8));
}

.winner-selection-content h2 {
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #ffffff 0%, #8b5cf6 50%, #a855f7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 30px rgba(139, 92, 246, 0.5);
}

.winner-selection-content p {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
  font-weight: 500;
}

.loading-dots {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.loading-dots span {
  width: 10px;
  height: 10px;
  background: linear-gradient(135deg, #8b5cf6, #a855f7);
  border-radius: 50%;
  animation: loadingDots 1.4s ease-in-out infinite both;
  box-shadow: 0 0 10px rgba(139, 92, 246, 0.5);
}

.loading-dots span:nth-child(1) { animation-delay: -0.32s; }
.loading-dots span:nth-child(2) { animation-delay: -0.16s; }
.loading-dots span:nth-child(3) { animation-delay: 0s; }

@keyframes spinAndPulse {
  0%, 100% { 
    transform: rotate(0deg) scale(1); 
    opacity: 0.8;
  }
  25% { 
    transform: rotate(90deg) scale(1.1); 
    opacity: 1;
  }
  50% { 
    transform: rotate(180deg) scale(1.2); 
    opacity: 1;
  }
  75% { 
    transform: rotate(270deg) scale(1.1); 
    opacity: 1;
  }
}

@keyframes loadingDots {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1.2);
    opacity: 1;
  }
}

/* Mobile responsive for loading overlay */
@media (max-width: 480px) {
  .winner-selection-content {
    padding: 2rem 1.5rem;
    border-radius: 20px;
  }
  
  .loading-spinner-big {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .winner-selection-content h2 {
    font-size: 1.6rem;
    margin-bottom: 0.8rem;
  }
  
  .winner-selection-content p {
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .loading-dots span {
    width: 10px;
    height: 10px;
  }
  
  .bet-confirmation-details {
    margin: 1rem 0;
    padding: 0.8rem;
  }
  
  .wager-status {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .current-wager,
  .expected-wager {
    font-size: 0.8rem;
    padding: 0.4rem;
  }
  
  .progress-text {
    font-size: 0.8rem;
  }
}

/* Smooth transitions for all number values */
[class*="amount"],
[class*="value"],
[class*="count"] {
  transition: all 0.3s ease;
  will-change: contents;
}

.jackpot-value {
  transition: all 0.3s ease;
}

.your-wager {
  transition: all 0.3s ease;
}

.your-chance {
  transition: all 0.3s ease;
}

/* Transaction Notification */
.transaction-notification {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  z-index: 1000;
  animation: slideInFromRight 0.3s ease-out;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border: 1px solid;
  max-width: 300px;
  word-wrap: break-word;
}

.transaction-notification.success {
  background: linear-gradient(135deg, rgba(16, 185, 129, 0.9), rgba(34, 197, 94, 0.9));
  color: white;
  border-color: rgba(16, 185, 129, 0.5);
  box-shadow: 0 4px 20px rgba(16, 185, 129, 0.3);
}

.transaction-notification.pending {
  background: linear-gradient(135deg, rgba(245, 158, 11, 0.9), rgba(251, 191, 36, 0.9));
  color: white;
  border-color: rgba(245, 158, 11, 0.5);
  box-shadow: 0 4px 20px rgba(245, 158, 11, 0.3);
}

.transaction-notification.error {
  background: linear-gradient(135deg, rgba(239, 68, 68, 0.9), rgba(248, 113, 113, 0.9));
  color: white;
  border-color: rgba(239, 68, 68, 0.5);
  box-shadow: 0 4px 20px rgba(239, 68, 68, 0.3);
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Mobile responsive for notification */
@media (max-width: 480px) {
  .transaction-notification {
    top: 10px;
    right: 10px;
    left: 10px;
    max-width: none;
    font-size: 12px;
    padding: 10px 16px;
  }
} 