.player-slot {
  background: rgba(26, 26, 46, 0.6);
  border: 1px solid rgba(139, 92, 246, 0.2);
  border-radius: 16px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 14px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  min-height: 140px;
  backdrop-filter: blur(20px);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  cursor: pointer;
  overflow: hidden;
}

.player-slot::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.5), transparent);
}

.player-slot::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(139, 92, 246, 0.05) 50%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.player-slot:hover::after {
  opacity: 1;
}

.player-slot:hover {
  border-color: rgba(139, 92, 246, 0.5);
  transform: translateY(-6px) scale(1.02);
  box-shadow: 
    0 12px 40px rgba(0, 0, 0, 0.3),
    0 0 30px rgba(139, 92, 246, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.player-slot.waiting {
  opacity: 0.7;
  border-style: dashed;
  border-color: rgba(139, 92, 246, 0.3);
  background: rgba(26, 26, 46, 0.3);
}

.player-slot.waiting:hover {
  opacity: 0.8;
  border-color: rgba(139, 92, 246, 0.4);
  transform: translateY(-3px) scale(1.01);
}

.player-slot.active {
  border-color: rgba(139, 92, 246, 0.5);
  background: rgba(139, 92, 246, 0.08);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.2),
    0 0 20px rgba(139, 92, 246, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.player-slot.active::before {
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.7), transparent);
}

.player-avatar {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
  box-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.3),
    0 0 0 2px rgba(139, 92, 246, 0.2);
}

.player-slot:hover .player-avatar {
  transform: scale(1.1);
  box-shadow: 
    0 6px 25px rgba(0, 0, 0, 0.4),
    0 0 0 3px rgba(139, 92, 246, 0.4);
}

.waiting-avatar {
  width: 100%;
  height: 100%;
  background: rgba(26, 26, 46, 0.8);
  border: 2px dashed rgba(139, 92, 246, 0.4);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(139, 92, 246, 0.6);
  transition: all 0.3s ease;
  animation: pulse 3s ease-in-out infinite;
}

.player-slot.waiting:hover .waiting-avatar {
  border-color: rgba(139, 92, 246, 0.6);
  color: rgba(139, 92, 246, 0.8);
}

.avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.player-slot:hover .avatar-image {
  transform: scale(1.05);
}

.generated-avatar {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 20px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
}

.generated-avatar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: inherit;
  border-radius: 50%;
  filter: brightness(1.2);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.player-slot:hover .generated-avatar::before {
  opacity: 0.3;
}

.player-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  flex: 1;
}

.player-name {
  font-weight: 700;
  color: white;
  font-size: 15px;
  text-align: center;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: all 0.3s ease;
  position: relative;
}

.player-slot.active .player-name {
  background: linear-gradient(135deg, #8b5cf6 0%, #a855f7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.player-slot.waiting .player-name {
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
  font-weight: 500;
}

.player-stats {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.player-bet {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 13px;
  padding: 4px 8px;
  background: rgba(15, 15, 35, 0.6);
  border-radius: 8px;
  border: 1px solid rgba(139, 92, 246, 0.2);
  transition: all 0.3s ease;
}

.player-slot.active .player-bet {
  background: rgba(139, 92, 246, 0.1);
  border-color: rgba(139, 92, 246, 0.3);
}

.stat-symbol {
  color: rgba(139, 92, 246, 0.8);
  font-weight: 600;
}

.stat-value {
  color: white;
  font-weight: 700;
}

.player-chance {
  font-size: 11px;
  color: #10b981;
  font-weight: 700;
  background: rgba(16, 185, 129, 0.15);
  padding: 4px 10px;
  border-radius: 12px;
  border: 1px solid rgba(16, 185, 129, 0.3);
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 10px rgba(16, 185, 129, 0.2);
}

.player-slot:hover .player-chance {
  background: rgba(16, 185, 129, 0.2);
  border-color: rgba(16, 185, 129, 0.4);
  box-shadow: 0 4px 15px rgba(16, 185, 129, 0.3);
}

.player-status {
  position: absolute;
  top: 12px;
  right: 12px;
}

.status-dot {
  width: 10px;
  height: 10px;
  background: #10b981;
  border-radius: 50%;
  animation: statusPulse 2s infinite;
  box-shadow: 
    0 0 0 0 rgba(16, 185, 129, 0.4),
    0 2px 5px rgba(16, 185, 129, 0.3);
  position: relative;
}

.status-dot::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: inherit;
  border-radius: 50%;
  opacity: 0.3;
  animation: statusRipple 2s infinite;
}

@keyframes statusPulse {
  0% {
    box-shadow: 
      0 0 0 0 rgba(16, 185, 129, 0.4),
      0 2px 5px rgba(16, 185, 129, 0.3);
  }
  70% {
    box-shadow: 
      0 0 0 8px rgba(16, 185, 129, 0),
      0 2px 5px rgba(16, 185, 129, 0.3);
  }
  100% {
    box-shadow: 
      0 0 0 0 rgba(16, 185, 129, 0),
      0 2px 5px rgba(16, 185, 129, 0.3);
  }
}

@keyframes statusRipple {
  0% {
    transform: scale(1);
    opacity: 0.3;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

/* Enhanced waiting slot animation */
@keyframes waitingGlow {
  0%, 100% {
    border-color: rgba(139, 92, 246, 0.3);
    box-shadow: 0 0 0 rgba(139, 92, 246, 0);
  }
  50% {
    border-color: rgba(139, 92, 246, 0.5);
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.2);
  }
}

.player-slot.waiting {
  animation: waitingGlow 4s ease-in-out infinite;
}

/* Responsive Design */
@media (max-width: 768px) {
  .player-slot {
    padding: 16px;
    min-height: 120px;
    gap: 12px;
  }
  
  .player-avatar {
    width: 48px;
    height: 48px;
  }
  
  .generated-avatar {
    font-size: 18px;
  }
  
  .player-name {
    font-size: 14px;
  }
  
  .player-bet {
    font-size: 12px;
    padding: 3px 6px;
  }
  
  .player-chance {
    font-size: 10px;
    padding: 3px 8px;
  }
  
  .status-dot {
    width: 8px;
    height: 8px;
  }
}

@media (max-width: 480px) {
  .player-slot {
    padding: 14px;
    min-height: 110px;
  }
  
  .player-avatar {
    width: 44px;
    height: 44px;
  }
  
  .generated-avatar {
    font-size: 16px;
  }
  
  .player-name {
    font-size: 13px;
  }
}

/* Mobile Player Slot */
.mobile-player-slot {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  padding: 8px;
  background: rgba(24, 24, 27, 0.9);
  border: 1px solid rgba(139, 92, 246, 0.2);
  border-radius: 8px;
  transition: all 0.3s ease;
  min-height: 90px;
  justify-content: center;
  position: relative;
  overflow: hidden;
  transform-origin: center center;
}

.mobile-player-slot::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.3), transparent);
}

.mobile-player-slot.waiting {
  background: rgba(16, 16, 16, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: waitingPulse 3s ease-in-out infinite;
}

@keyframes waitingPulse {
  0%, 100% {
    border-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 0 rgba(139, 92, 246, 0);
  }
  50% {
    border-color: rgba(139, 92, 246, 0.3);
    box-shadow: 0 0 10px rgba(139, 92, 246, 0.2);
  }
}

.mobile-player-slot.active {
  border-color: rgba(139, 92, 246, 0.3);
  background: rgba(24, 24, 27, 0.9);
}

/* Predetermined Winner State */
.mobile-player-slot.predetermined {
  border: 2px solid #ff6b35;
  background: rgba(255, 107, 53, 0.1);
  box-shadow: 0 0 20px rgba(255, 107, 53, 0.3);
  animation: predeterminedGlow 1s ease-in-out infinite;
}

@keyframes predeterminedGlow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(255, 107, 53, 0.3);
    border-color: #ff6b35;
  }
  50% {
    box-shadow: 0 0 30px rgba(255, 107, 53, 0.5);
    border-color: #ff8c6b;
  }
}

/* Mobile Player Avatar */
.mobile-player-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
}

.waiting-icon {
  width: 100%;
  height: 100%;
  background: rgba(60, 60, 65, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.4);
  font-size: 16px;
  font-weight: 700;
  transition: all 0.3s ease;
}

.mobile-player-slot.waiting:hover .waiting-icon {
  border-color: rgba(139, 92, 246, 0.4);
  color: rgba(139, 92, 246, 0.7);
  background: rgba(139, 92, 246, 0.1);
}

.generated-avatar {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 14px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

.avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

/* Mobile Player Info */
.mobile-player-info {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 2px;
  z-index: 1;
  width: 100%;
}

.mobile-player-name {
  font-size: 11px;
  font-weight: 600;
  color: #ffffff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 70px;
  transition: all 0.3s ease;
}

.mobile-player-slot.waiting .mobile-player-name {
  color: rgba(255, 255, 255, 0.4);
  font-style: italic;
}

.mobile-player-slot.active .mobile-player-name {
  color: rgba(255, 255, 255, 0.9);
}

.mobile-player-bet {
  font-size: 10px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  padding: 1px 4px;
  background: rgba(139, 92, 246, 0.2);
  border-radius: 4px;
  border: 1px solid rgba(139, 92, 246, 0.3);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
}

.mobile-player-slot.waiting .mobile-player-bet {
  color: rgba(255, 255, 255, 0.3);
  background: rgba(60, 60, 65, 0.4);
  border-color: rgba(255, 255, 255, 0.1);
}

.mobile-player-slot.active .mobile-player-bet {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.3);
  color: rgba(255, 255, 255, 0.8);
}

/* Winner State */
.mobile-player-slot.winner {
  border: 2px solid #ffd700;
  background: rgba(255, 215, 0, 0.15);
  box-shadow: 0 0 25px rgba(255, 215, 0, 0.4);
  animation: winnerGlow 1s ease-in-out infinite;
  transform: scale(1.05);
}

@keyframes winnerGlow {
  0%, 100% {
    box-shadow: 0 0 25px rgba(255, 215, 0, 0.4);
    border-color: #ffd700;
  }
  50% {
    box-shadow: 0 0 35px rgba(255, 215, 0, 0.6);
    border-color: #ffed4e;
  }
}

.winner-crown {
  position: absolute;
  top: -6px;
  right: -6px;
  font-size: 16px;
  animation: crownBounce 0.8s ease-out;
  filter: drop-shadow(0 0 6px rgba(255, 215, 0, 0.8));
  z-index: 10;
}

.predetermined-indicator {
  position: absolute;
  top: -6px;
  right: -6px;
  font-size: 14px;
  animation: targetPulse 1s ease-in-out infinite;
  filter: drop-shadow(0 0 6px rgba(255, 107, 53, 0.8));
  z-index: 10;
}

@keyframes targetPulse {
  0%, 100% { transform: scale(1); opacity: 0.8; }
  50% { transform: scale(1.2); opacity: 1; }
}

@keyframes crownBounce {
  0% { transform: scale(0); opacity: 0; }
  50% { transform: scale(1.2); opacity: 0.8; }
  100% { transform: scale(1); opacity: 1; }
}

.winner-text {
  color: #ffd700;
  font-size: 8px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  animation: winnerTextGlow 1s ease-in-out infinite;
  margin-top: 2px;
}

.predetermined-text {
  color: #ff6b35;
  font-size: 8px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  animation: predeterminedTextGlow 1s ease-in-out infinite;
  margin-top: 2px;
}

@keyframes winnerTextGlow {
  0%, 100% { text-shadow: 0 0 4px rgba(255, 215, 0, 0.5); }
  50% { text-shadow: 0 0 8px rgba(255, 215, 0, 0.8); }
}

@keyframes predeterminedTextGlow {
  0%, 100% { text-shadow: 0 0 4px rgba(255, 107, 53, 0.5); }
  50% { text-shadow: 0 0 8px rgba(255, 107, 53, 0.8); }
}

/* Win Chance Display */
.win-chance {
  font-size: 8px;
  color: #10b981;
  font-weight: 600;
  background: rgba(16, 185, 129, 0.15);
  padding: 1px 4px;
  border-radius: 4px;
  border: 1px solid rgba(16, 185, 129, 0.3);
  text-transform: uppercase;
  letter-spacing: 0.2px;
  margin-top: 1px;
}

/* Hover Effects */
.mobile-player-slot:hover {
  transform: translateY(-2px);
  border-color: rgba(139, 92, 246, 0.4);
}

.mobile-player-slot.waiting:hover {
  border-color: rgba(139, 92, 246, 0.3);
  background: rgba(139, 92, 246, 0.05);
}

.mobile-player-slot:hover .mobile-player-avatar {
  transform: scale(1.05);
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .mobile-player-slot {
    padding: 6px;
    min-height: 80px;
    gap: 4px;
  }
  
  .mobile-player-avatar {
    width: 28px;
    height: 28px;
  }
  
  .waiting-icon {
    font-size: 14px;
  }
  
  .generated-avatar {
    font-size: 12px;
  }
  
  .mobile-player-name {
    font-size: 10px;
    max-width: 60px;
  }
  
  .mobile-player-bet {
    font-size: 9px;
    padding: 1px 3px;
  }
  
  .win-chance {
    font-size: 7px;
    padding: 1px 3px;
  }
  
  .winner-crown, .predetermined-indicator {
    font-size: 14px;
    top: -4px;
    right: -4px;
  }
} 