.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  background: 
    radial-gradient(circle at 20% 20%, rgba(139, 92, 246, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(168, 85, 247, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 40% 60%, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
    linear-gradient(135deg, #0f0f23 0%, #1a1a2e 25%, #16213e 50%, #0f172a 75%, #0c1220 100%);
}

.app-container {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 0;
  margin: 0;
  width: 100%;
  max-width: 1500px;
  margin: 0 auto;
  position: relative;
  padding-bottom: 80px; /* Space for mobile footer */
}

.main-content {
  background: transparent;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex: 1;
  order: 1;
}

/* Hide desktop sidebars */
.sidebar-left,
.sidebar-right {
  display: none;
}

/* Mobile Footer Navigation */
.mobile-footer {
  display: block;
  background: rgba(15, 15, 35, 0.95);
  border-top: 1px solid rgba(139, 92, 246, 0.3);
  padding: 12px 16px;
  backdrop-filter: blur(20px);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  box-shadow: 0 -4px 20px rgba(0, 0, 0, 0.3);
  width: 100vw;
  margin-left: calc(-50vw + 50%);
}

.mobile-footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1500px;
  margin: 0 auto;
  padding: 0 16px;
}

.mobile-stats {
  display: flex;
  gap: 20px;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
}

.mobile-stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
}

.mobile-stat-value {
  font-weight: 700;
  color: white;
  font-size: 13px;
}

.mobile-stat-label {
  font-size: 10px;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.mobile-games-btn {
  background: linear-gradient(135deg, #8b5cf6 0%, #a855f7 100%);
  border: none;
  border-radius: 8px;
  padding: 10px 16px;
  color: white;
  font-weight: 600;
  font-size: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: all 0.3s ease;
  box-shadow: 
    0 4px 15px rgba(139, 92, 246, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.mobile-games-btn:hover {
  transform: translateY(-1px);
  box-shadow: 
    0 6px 20px rgba(139, 92, 246, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

/* Chat Tab */
.mobile-chat-section {
  background: rgba(15, 15, 35, 0.8);
  border-radius: 16px;
  border: 1px solid rgba(139, 92, 246, 0.25);
  backdrop-filter: blur(20px);
  margin: 16px;
  overflow: hidden;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* Leaderboard Tab */
.mobile-leaderboard-section {
  background: rgba(15, 15, 35, 0.8);
  border-radius: 16px;
  border: 1px solid rgba(139, 92, 246, 0.25);
  backdrop-filter: blur(20px);
  margin: 16px;
  overflow: hidden;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* Tab Navigation */
.mobile-tab-nav {
  display: flex;
  background: rgba(26, 26, 46, 0.6);
  border-radius: 12px;
  padding: 4px;
  margin: 16px;
  border: 1px solid rgba(139, 92, 246, 0.2);
}

.mobile-tab {
  flex: 1;
  padding: 12px 16px;
  background: transparent;
  border: none;
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.mobile-tab.active {
  background: linear-gradient(135deg, #8b5cf6 0%, #a855f7 100%);
  color: white;
  box-shadow: 
    0 4px 15px rgba(139, 92, 246, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.mobile-tab:hover:not(.active) {
  background: rgba(139, 92, 246, 0.1);
  color: white;
}

/* Content sections */
.mobile-content-section {
  display: none;
}

.mobile-content-section.active {
  display: block;
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .app-container {
    max-width: 100%;
  }
  
  .main-content {
    padding: 12px;
    gap: 16px;
  }
  
  .mobile-footer {
    padding: 10px 12px;
  }
  
  .mobile-stats {
    gap: 12px;
  }
  
  .mobile-stat {
    gap: 1px;
  }
  
  .mobile-stat-value {
    font-size: 12px;
  }
  
  .mobile-stat-label {
    font-size: 9px;
  }
  
  .mobile-games-btn {
    padding: 8px 12px;
    font-size: 11px;
  }
  
  .mobile-tab-nav {
    margin: 12px;
  }
  
  .mobile-tab {
    padding: 8px 12px;
    font-size: 13px;
  }
  
  .mobile-chat-section,
  .mobile-leaderboard-section {
    margin: 12px;
    border-radius: 12px;
  }
}

@media (max-width: 360px) {
  .main-content {
    padding: 8px;
    gap: 12px;
  }
  
  .mobile-footer {
    padding: 8px 10px;
  }
  
  .mobile-stats {
    gap: 8px;
  }
  
  .mobile-stat-value {
    font-size: 11px;
  }
  
  .mobile-stat-label {
    font-size: 8px;
  }
  
  .mobile-games-btn {
    padding: 6px 10px;
    font-size: 10px;
  }
  
  .mobile-tab-nav {
    margin: 8px;
  }
  
  .mobile-chat-section,
  .mobile-leaderboard-section {
    margin: 8px;
  }
}

/* Admin tab styling */
.nav-tab.admin-tab {
  background: linear-gradient(135deg, #dc2626, #ef4444);
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.nav-tab.admin-tab:hover {
  background: linear-gradient(135deg, #b91c1c, #dc2626);
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3);
}

/* Wallet info styling */
.wallet-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 4px;
  margin-right: 16px;
}

.wallet-address {
  font-size: 0.8rem;
  color: #9ca3af;
  font-family: 'Courier New', monospace;
}

.admin-badge {
  font-size: 0.7rem;
  background: linear-gradient(135deg, #dc2626, #ef4444);
  color: white;
  padding: 2px 8px;
  border-radius: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Loading overlay */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  z-index: 1000;
  color: white;
  font-size: 1.1rem;
}

.loading-spinner {
  font-size: 2rem;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Mobile admin section */
.mobile-admin-section {
  padding: 16px;
  height: 100%;
  overflow-y: auto;
}

/* Enhanced mobile stats to include win chance */
.mobile-stat:last-child {
  border-left: 1px solid rgba(139, 92, 246, 0.2);
  padding-left: 12px;
}

/* Responsive adjustments for wallet info */
@media (max-width: 768px) {
  .wallet-info {
    display: none; /* Hide detailed wallet info on mobile */
  }
  
  .admin-badge {
    font-size: 0.6rem;
    padding: 1px 6px;
  }
  
  .loading-overlay {
    font-size: 1rem;
  }
  
  .loading-spinner {
    font-size: 1.5rem;
  }
}

/* Automation tab styling */
.nav-tab.automation-tab {
  background: linear-gradient(135deg, #06b6d4, #0891b2);
  border: 1px solid rgba(6, 182, 212, 0.3);
  position: relative;
}

.nav-tab.automation-tab:hover {
  background: linear-gradient(135deg, #0891b2, #0e7490);
  box-shadow: 0 4px 15px rgba(6, 182, 212, 0.3);
}

.automation-indicator {
  position: absolute;
  top: -4px;
  right: -4px;
  font-size: 8px;
  background: rgba(34, 197, 94, 0.2);
  border-radius: 50%;
  padding: 2px;
  border: 1px solid rgba(34, 197, 94, 0.5);
}

.mobile-automation-indicator {
  position: absolute;
  top: -2px;
  right: -2px;
  font-size: 6px;
  background: rgba(34, 197, 94, 0.2);
  border-radius: 50%;
  padding: 1px;
  border: 1px solid rgba(34, 197, 94, 0.5);
}

/* Automation badge styling */
.automation-badge {
  font-size: 0.7rem;
  background: linear-gradient(135deg, #06b6d4, #0891b2);
  color: white;
  padding: 2px 8px;
  border-radius: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Mobile automation section */
.mobile-automation-section {
  padding: 16px;
  height: 100%;
  overflow-y: auto;
}

/* Automation notification */
.automation-notification {
  position: fixed;
  top: 20px;
  right: 20px;
  background: rgba(6, 182, 212, 0.15);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(6, 182, 212, 0.3);
  border-radius: 12px;
  padding: 12px 16px;
  color: #06b6d4;
  font-size: 0.9rem;
  font-weight: 600;
  z-index: 900;
  animation: slideInRight 0.3s ease-out;
  box-shadow: 0 4px 20px rgba(6, 182, 212, 0.1);
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Mobile responsiveness for automation elements */
@media (max-width: 768px) {
  .automation-notification {
    top: 10px;
    right: 10px;
    left: 10px;
    font-size: 0.8rem;
    padding: 10px 12px;
    text-align: center;
  }
  
  .automation-badge {
    font-size: 0.6rem;
    padding: 1px 6px;
  }
} 